import React, {useState, useEffect, useRef} from 'react'
import '../Styles/Stats.css'
import {onValue, ref as dbRef} from 'firebase/database'
import {db} from './FirebaseSetup'

function Stats(props) {
  
  const [games, setGames] = useState([])

  const imgDisplay = useRef()

  useEffect(()=>{
    loadStats()
  },[])

  function imageDrop(event){
    event.preventDefault()
    var file = event.target.files[0]
    // save the file
    // create a url
    // display the file
  }
  function imageHover(event){
    event.preventDefault()
  }
  function saveProfileInfo(){

  }
  function openSignUp(){
    // Maybe just have it embeded so its easier
  }
  function loadStats(){
    console.log("loading stats. userId: "+props.userId)
    onValue(dbRef(db, "typingGame/users/"+props.userId+"/games/"), snap=>{
      var tempArray = []
      snap.forEach(gameSnap=>{
        tempArray.push({
          date:gameSnap.child("dateTime").val(),
          points:gameSnap.child("points").val(),
        })
      })
      setGames(tempArray)
    })
  }

  return (
    <div className='statsWindow'>
      <div className='closeButton' onClick={props.close}>X</div>
      <div className='statsWindowSection profileSection'>
        <div className='imageDisplay'>
          <img></img>
        </div>
        <div className='statsWindowSection inputDiv'>
          <input className='input' placeholder='name'></input>
          <textarea className='input textarea' placeholder='about'></textarea>
        </div>
      </div>
      <div className='statsWindowSection gamesDisplay'>
        {games.map(game=>(
          <div className='gameLine'>{game.date} _____ {game.points} Points</div>
        ))}
      </div>      
    </div>
  )
}

export default Stats