  function generateLetterArray(word){
    // For each letter add the correspondig word to an array and return it
    var tempArray = []
    for(var x=0; x<word.length; x++)
      tempArray.push(wordFromLetter(word.charAt(x), x%2))    

    return tempArray
  }
