import React, {useState, useRef} from 'react'
import '../Styles/Auth.css'
import {signInWithEmailAndPassword, createUserWithEmailAndPassword} from 'firebase/auth'
import {auth, db} from './FirebaseSetup'
import Stats from './Stats'
import {ref, set} from 'firebase/database'

function Auth(props) {
    
    const [type, setType] = useState("signIn")
    

    const nameRef = useRef()
    const emailRef = useRef()
    const passRef = useRef()

    function signIn(){        
        signInWithEmailAndPassword(auth, emailRef.current.value, passRef.current.value).then(user=>{
            // This will only be reached if sign in is successful
            props.close()
        })
        .catch(error=>{
            console.log("error signing in: "+error)
        })
    }
    function signUp(){        
        createUserWithEmailAndPassword(auth, emailRef.current.value, passRef.current.value)
        .then(user=>{            
            if(user){
                console.log("signed up user: "+user.user.uid)
                // create user in db
                set(ref(db, "typingGame/users/"+user.user.uid), {
                    name:nameRef.current.value,
                    games:"none yet"
                })
                props.close()
            }            
        })
        .catch(error=>{
            console.log("error signing up: "+error)
        })
    }

    return (
    <div className='authWindow'>
        SignIn Window
        <div className='closeButton' onClick={props.close}>x</div>
            {props.signInType === "signUp" &&
                <input 
                    className='inputField' 
                    placeholder='name'
                    ref={nameRef}
                ></input>                
            }
            <input 
                className='inputField' 
                placeholder='email'
                ref={emailRef}
            ></input>
            <input 
                className='inputField' 
                placeholder='password'
                ref={passRef}
            ></input>
        
        <div className='buttonContainer'>
            <div className='buttonContainerFlex'>
                <button className='button buttonWide' onClick={props.close}>Close</button>                
                {props.signInType === "signIn" &&                    
                    <button className='button buttonWide' onClick={signIn}>Sign In</button>
                }
                {props.signInType === "signUp" &&                    
                    <button className='button buttonWide' onClick={signUp}>Sign Up</button>
                }
            </div>
        </div>
    </div>
  )
}

export default Auth